import TextField from '@mui/material/TextField';
import React, { useRef, useState } from 'react';
import { viewSetViewProps } from './props';
import { resetDecimalInputValue } from './decimalnum';
import { updateBinaryInputValue } from './binarynum';

const getHexString = (view1: DataView) => {
  let hexString = view1.getUint32(0).toString(16);
  while (hexString.length < 8) {
    hexString = '0' + hexString;
  }
  return hexString;
}

let inputVal1: React.RefObject<HTMLInputElement> | null = null;
let setError1: ((value: boolean) => void) | null = null;

// When the view is changed, someone needs to call this function
export const updateHexInputValue = (view: DataView) => {
  if (inputVal1) {
    if (inputVal1.current) {
      inputVal1.current.value = getHexString(view);
    }
  }
  if (setError1) {
    setError1(false);
  }
}

export default function HexNum({view, setView}: viewSetViewProps) {

  const inputVal = useRef<HTMLInputElement>(null);
  inputVal1 = inputVal;

  const [error, setError] = useState(false);
  setError1 = setError;

  const handleInputChange = () => {
    if (inputVal.current === null) return;
    const newValue = inputVal.current.value;

    // check for error
    const isValid = /^[0-9a-fA-F]{0,8}$/.test(newValue);
    setError(!isValid);

    const newView = new DataView(view.buffer);
    newView.setUint32(0, parseInt(newValue, 16));
    setView(newView);

    resetDecimalInputValue();
    updateBinaryInputValue(newView);
  }

  return (
    <tr>
      <td>Hex num</td>
      <td colSpan={3} style={{fontFamily: 'JetBrains Mono'}}>
        <TextField 
          inputProps={{ style: { fontFamily: 'JetBrains Mono' } }}
          FormHelperTextProps={{ style: { fontFamily: 'JetBrains Mono' } }}
          fullWidth
          error={error}
          helperText={error ? "Input is Invalid or Too Long" : "Enter hexadecimal value here"}
          variant="standard" 
          inputRef={inputVal}
          onChange={handleInputChange}
        />
      </td>
    </tr>
  )
}