import { viewSetViewProps } from "./props";

const getBitString = (view1: DataView): string => {
  let bitString = view1.getUint32(0).toString(2);
  while (bitString.length < 32) {
    bitString = '0' + bitString;
  }
  return bitString;
}

export default function Meaning({ view, setView }: viewSetViewProps) {

  const exp_int: number = parseInt(getBitString(view).slice(1, 9), 2);
  const man_int: number = parseInt(getBitString(view).slice(9), 2);

  // exponent
  const exp_meaning = () => {
    
    if (exp_int === 0) {
      return (
        <>
          2<span><sup>-126</sup></span> (denorm)
        </>
      )
    } 
    else if (exp_int === 255) {
      if (man_int === 0) {
        return (
          <>
            &plusmn;Infinity
          </>
        )
      }
      else {
        return (
          <>
            NaN
          </>
        )
      
      }
    }
    else {
      return (
        <>
          2<span><sup>{exp_int - 127}</sup></span>
        </>
      )
    }
  }



  return (
    <tr>
      <td style={{fontSize: 'larger'}}>Meaning</td>
      <td>
        <span style={{textAlign: 'center', fontFamily: 'JetBrains Mono', fontSize: 'larger'}}>
          {getBitString(view)[0] === '1' ? '+1' : '-1'}
        </span>
      </td>
      <td>
        <span style={{textAlign: 'center', fontFamily: 'JetBrains Mono', fontSize: 'larger'}}>
          {exp_meaning()}
        </span>
      </td>
      <td>
        <span style={{textAlign: 'center', fontFamily: 'JetBrains Mono', fontSize: 'larger'}}>
          {(exp_int === 0 ? '0.':'1.') + getBitString(view).slice(9)}
        </span>
      </td>
    </tr>
  )
}