import TextField from '@mui/material/TextField';
import { useRef, useState } from 'react';
import { viewSetViewProps } from './props';
import { updateHexInputValue } from './hexnum';
import { updateBinaryInputValue } from './binarynum';

let inputVal1: React.RefObject<HTMLInputElement> | null = null;
let setError1: ((value: boolean) => void) | null = null;

export const resetDecimalInputValue = () => {
  if (inputVal1) {
    if (inputVal1.current) {
      inputVal1.current.value = '';
    }
  }
  if (setError1) {
    setError1(false);
  }
}

export default function DecimalNum({view, setView}: viewSetViewProps) {

  const inputVal = useRef<HTMLInputElement>(null);
  inputVal1 = inputVal;

  const [error, setError] = useState(false);
  setError1 = setError;

  const handleInputChange = () => {
    if (inputVal.current === null) return;
    const newValue = inputVal.current.value;

    // check for error
    const isValid = /^-?[0-9]*(\.[0-9]+)?$/.test(newValue);
    setError(!isValid);

    const newView = new DataView(view.buffer);
    newView.setFloat32(0, parseFloat(newValue));
    setView(newView);

    updateHexInputValue(newView);
    updateBinaryInputValue(newView);
  }

  return (
    <tr>
      <td>Decimal num</td>
      <td colSpan={3} >
        <TextField 
          inputProps={{ style: { fontFamily: 'JetBrains Mono' } }}
          FormHelperTextProps={{ style: { fontFamily: 'JetBrains Mono' } }}
          fullWidth
          error={error}
          helperText={error ? "Input is not a valid decimal number" : "Enter decimal value here"}
          variant="standard" 
          inputRef={inputVal}
          onChange={handleInputChange}
        />
      </td>
    </tr>
  )
}