import TextField from '@mui/material/TextField';
import { useRef, useState } from 'react';
import { viewSetViewProps } from './props';
import { updateHexInputValue } from './hexnum';
import { resetDecimalInputValue } from './decimalnum';

const getBitString = (view1: DataView): string => {
  let bitString = view1.getUint32(0).toString(2);
  while (bitString.length < 32) {
    bitString = '0' + bitString;
  }
  return bitString;
}

let inputVal1: React.RefObject<HTMLInputElement> | null = null;
let setError1: ((value: boolean) => void) | null = null;

// When the view is changed, someone needs to call this function
export const updateBinaryInputValue = (view: DataView) => {
  if (inputVal1) {
    if (inputVal1.current) {
      inputVal1.current.value = getBitString(view);
    }
  }
  if (setError1) {
    setError1(false);
  }
}

export default function BinaryNum({view, setView}: viewSetViewProps) {

  const inputVal = useRef<HTMLInputElement>(null);
  inputVal1 = inputVal;

  const [error, setError] = useState(false);
  setError1 = setError;

  // useEffect(() => {
  //   setInputValue(getBitString(view));
  // }, [view]);

  const handleInputChange = () => {
    if (inputVal.current === null) return;
    const newValue = inputVal.current.value;

    // check for error
    const isValid = /^(?:0[bB])?[0-1]{0,32}$/.test(newValue);
    setError(!isValid);

    const newView = new DataView(view.buffer);
    newView.setUint32(0, parseInt(newValue, 2));
    setView(newView);

    updateHexInputValue(newView);
    resetDecimalInputValue();
  }

  return (
    <tr>
      <td>Hex num</td>
      <td colSpan={3} style={{fontFamily: 'JetBrains Mono'}}>
        <TextField 
          inputProps={{ style: { fontFamily: 'JetBrains Mono' } }}
          FormHelperTextProps={{ style: { fontFamily: 'JetBrains Mono' } }}
          fullWidth
          error={error}
          helperText={error ? "Input is not a valid decimal number" : "Enter binary value here"}
          variant="standard" 
          inputRef={inputVal}
          onChange={handleInputChange}
        />
      </td>
    </tr>
  )
}