import * as React from 'react';

import '../vendor/material.icons.css';
import '../vendor/material.indigo-pink.min.css';
import '../vendor/material.min.js';

import BinaryCheckboxes from './parts/binary';
import Meaning from './parts/meaning';
import RepresentedValue from './parts/representedvalue';
import BinaryNum from './parts/binarynum';
import DecimalNum from './parts/decimalnum';
import HexNum from './parts/hexnum';

const buffer = new ArrayBuffer(4);
const dataview = new DataView(buffer);
dataview.setFloat32(0, 3);


export default function Home() {

  // base data type holding the floating point number
  
  const [view, setView] = React.useState(dataview);
  console.log(view.getUint32(0).toString(2));

  return (
    <div>
      <table className='mdl-data-table mdl-js-data-table'>
        <thead>
          <tr>
            <td></td>
            <td style={{fontSize: 'larger', textAlign: 'center'}}>Sign</td>
            <td style={{fontSize: 'larger', textAlign: 'center'}}>Exponent</td>
            <td style={{fontSize: 'larger', textAlign: 'center'}}>Mantissa</td>
          </tr>
        </thead>
        <tbody>
          <BinaryCheckboxes view={view} setView={setView} />
          <Meaning view={view} setView={setView} />
          <RepresentedValue view={view} />
          <BinaryNum view={view} setView={setView} />
          <DecimalNum view={view} setView={setView} />
          <HexNum view={view} setView={setView} />
        </tbody>
      </table>
    </div>
  )
}